import axios from 'axios';
import router from '@/router'
import {getCookie,setCookie} from './cookie';
axios.defaults.timeout = 5000;
axios.defaults.baseURL ='';
var baseUrl = ''
if(process.env.NODE_ENV == 'development'){ // development // production
  baseUrl = '/api';
}

//http request 
axios.interceptors.request.use(
  config => {
    //x-user-lg
    const token = getCookie('userLg');//cookiejs-cookie
    config.data = JSON.stringify(config.data);
    //   if(token[0] == '1'){
    //
    //   }

    config.headers = {
      'Content-Type':'application/x-www-form-urlencoded',
        // 'cookie':cookie
    }
    // if(token){
    //   config.params = {'token':token}
    // }
    return config;
  },
  error => {
    return Promise.reject(err);
  }
);


//http response 
axios.interceptors.response.use(
  response => {
    //x-user-lgx-user-lgcookie
    if (response.headers["x-user-lg"]) {
      const cookie = response.headers["x-user-lg"].split(";");
      if (response.headers) {
        if (response.headers.responsecode == 1 && cookie[0] == 1) {
          router.push({
            path:"/login",
            querry:{redirect:router.currentRoute.fullPath}//
          })
        }
      }
    }
      //   if(cookie['UserLg'][0] != 1 && response.headers.get('x-user-lg') == cookie['UserLg']){
      //       // cookie user
      //   }
    if(response.data.errCode ==2){
      router.push({
        path:"/login",
        querry:{redirect:router.currentRoute.fullPath}//
      })
    }
    return response;
  },
  error => {
    return Promise.reject(error)
  }
)


/**
 * get
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url,params={}){
  return new Promise((resolve,reject) => {
    axios.get(baseUrl+url,{
      params:params
    })
      .then(response => {
        resolve(response.data);
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.post(baseUrl+url,data)
      .then(response => {
        resolve(response.data);
      },err => {
        reject(err)
      })
  })
}

/**
 * patch
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.patch(baseUrl+url,data)
      .then(response => {
        resolve(response.data);
      },err => {
        reject(err)
      })
  })
}

/**
 * put
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.put(baseUrl+url,data)
      .then(response => {
        resolve(response.data);
      },err => {
        reject(err)
      })
  })
}



